export class ASTNode {
    constructor(tagName = '') {
        this.children = []; // å­èŠ‚ç‚¹
        this.props = {}; // props
        this.type = 'element'; // èŠ‚ç‚¹ç±»å‹
        this.exprs = [];
        this.isSVG = false; // æ˜¯ä¸æ˜¯svgèŠ‚ç‚¹
        this.tagName = tagName;
        // åˆ¤æ–­æ˜¯ä¸æ˜¯ç»„ä»¶
        if (/^[A-Z]/.test(tagName)) {
            this.type = 'component';
        }
    }
}
