// æ€§èƒ½ä¼˜åŒ–ï¼Œè¿™å¯åœ¨startToken matchåï¼ŒæŸ¥çœ‹åé¢æ˜¯å¦æœ‰arr.lengthæ•°é‡çš„endToken
export function getMatched(start = '<%', end = '%>', str = '') {
    const START_LEN = start.length;
    const END_LEN = end.length;
    let index = 0;
    let arr = [];
    let result = {
        matchStr: '',
        value: ''
    };
    if (!str.startsWith(start)) {
        return result;
    }
    while (index < str.length) {
        // startToken
        if (str.slice(index, index + START_LEN) === start) {
            arr.push(false);
            index += START_LEN;
            continue;
        }
        else if (str.slice(index, index + END_LEN) === end) {
            index += END_LEN;
            arr.pop();
            if (arr.length === 0) {
                const matchStr = str.slice(0, index);
                return {
                    matchStr,
                    value: matchStr.slice(START_LEN, -END_LEN)
                };
            }
            continue;
        }
        // index++
        index += 1;
    }
    // åŒ¹é…å¤±è´¥
    return result;
}
